import React from 'react';
import { Menu, Icon,Button, Row } from 'antd';

const { SubMenu } = Menu;

const newFeedBack ={
  fontSize:25,
}

const Sidebar = {
  margin:5,
  padding:5
}

const AppSidebar = () => {
    return(
      <>
      <div style={Sidebar}>
        <Button
          type="primary"
          size='large'
          style={newFeedBack}
        > + 새로운 피드백 작성</Button>
      </div>
      <div style={Sidebar}>
        <Menu
            style={{ width: 256 }}
            defaultSelectedKeys={['1']}
            defaultOpenKeys={['sub1']}
            mode="inline"
        >
        <SubMenu
          key="sub1"
          title={
            <span>
              <Icon type="mail" />
              <span>Navigation One</span>
            </span>
          }
        >
          <Menu.ItemGroup key="g1" title="주제설정" style={{color:"black"}}>
            <Menu.Item key="1">Option 1</Menu.Item>
            <Menu.Item key="2">Option 2</Menu.Item>
          </Menu.ItemGroup>
        </SubMenu>
      </Menu>
      </div> 
      </> 
    );
};

export default AppSidebar;